import React, { useState } from 'react'
import { Nav, Container, Navbar, Button, NavDropdown } from 'react-bootstrap'
import { useAuth } from "../contexts/AuthContext"
import { NavLink, Link, useHistory } from 'react-router-dom'
import './CSS/NavBar.css'


function NavBar() {
    const [error, setError] = useState("")
    const { currentUser, logout } = useAuth()
    const history = useHistory()
  
    async function handleLogout() {
      setError("")
  
      try {
        await logout()
        history.push("/login")
      } catch {
        setError("Failed to log out")
      }
    }


    return (
        <div>
            


            <>
                <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Container>
                    <Navbar.Brand> <Link className="color__link" to="/">WSDV Web Portal</Link></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                             
                            <Link className='nav-link' to='/'>Home</Link>
                            
                            <Link className="nav-link" to="/videos">Videos</Link>
                            
                            
                            <NavDropdown title="Schedule" id="basic-nav-dropdown">
                                <NavDropdown.Item>
                                    <Link className="color__dropdown" to="/trainings">Trainings</Link>
                                </NavDropdown.Item>
                                <NavDropdown.Item>
                                    <Link className="color__dropdown" to="/schedule">Calendar</Link>
                                </NavDropdown.Item>
                               
                            </NavDropdown>
                        </Nav>
                        <Nav>
                        <Navbar.Collapse className="justify-content-end">
                            <Navbar.Text>
                                Signed in as: {currentUser.email} <Button variant="link" onClick={ handleLogout }>logout</Button>
                            </Navbar.Text>
                        </Navbar.Collapse>
                        </Nav>
                    </Navbar.Collapse>
                    </Container>
                </Navbar>
            </>
        </div>
    )
}

export default NavBar
